<template>
	<li class="card" @click="handlerCardClick(item)">
		<div class="thumb">
			<img :src="item.picUrl" alt="" />
			<span><i></i>{{ translatePlayCount(item.playCount) }}</span>
			<span></span>
		</div>
		<p class="name">{{ item.name }}</p>
	</li>
</template>

<script>
export default {
	name: "HomeRecommendCard",
	props: ["item"],
	data() {
		return {};
	},
	methods: {
		translatePlayCount(n) {
			if (n > 99999999) {
				return (n / 100000000).toFixed(2) + "亿";
			}
			if (n > 9999) {
				return (n / 10000).toFixed(1) + "万";
			}
		},
		handlerCardClick(param) {
			this.$router.push({ path: "playList", query: { id: param } });
		}
	}
};
</script>

<style lang="less">
.thumb {
	position: relative;
	img {
		vertical-align: middle;
		border-radius: 3px;
	}
	span {
		position: absolute;
		top: 5px;
		right: 5px;
		color: white;
		font-size: 10px;
			i {
		display: inline-block;
		padding-left: 15px;
		width: 10px;
		height: 10px;
		background-image: url("data:image/svg+xml;base64,");
		background-repeat: no-repeat;
	}
	}

}
.name {
	display: -webkit-box;
	font-size: 14px;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical;
	overflow: hidden;
	margin: 0 5px 10px 5px;
	padding: 6px 2px 0 6px;
	min-height: 30px;
	line-height: 1.2;
}
</style>
